.features-section {
    padding: 64px 0;
    background-color: #000000;
    color: #ffffff;
}

.features-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 16px;
}

.features-content {
    display: flex;
    flex-direction: column;
}

.features-text {
    width: 100%;
    margin-bottom: 32px;
}

.feature-item {
    margin-bottom: 48px;
    cursor: pointer;
    transition: opacity 0.3s ease;
    opacity: 0.5;
}

.feature-item.active {
    opacity: 1;
}

.feature-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 16px;
}

.feature-description {
    font-size: 16px;
    line-height: 1.5;
    color: #a0aec0;
}

.features-image {
    width: 100%;
    height: 400px;
    position: relative;
    overflow: hidden;
    border-radius: 8px;
}

.feature-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transition: opacity 0.5s ease;
}

.feature-img.active {
    opacity: 1;
}

@media (min-width: 768px) {
    .features-content {
        flex-direction: row;
    }

    .features-text {
        width: 50%;
        padding-right: 32px;
        margin-bottom: 0;
    }

    .features-image {
        width: 50%;
        height: 600px;
    }
}