.build-section {
    position: relative; 
    padding: 50px 0;
    background-color: #fbfbfb;
}

.build-section::before {
    content: '';
    position: absolute;
    top: 0; 
    left: 0;
    width: 100%;
    height: 2px; /* Высота линии */
    border-top: 2px dashed black; 
    border-radius: 30px 30px 0 0;
}

.title {
    font-size: 2.5rem;
    font-weight: bold;
    color: #111;
    margin-bottom: 10px;
}
.subtitle {
    font-size: 1.25rem;
    color: #555;
    margin-bottom: 30px;
}



.submit-btn {
    padding: 10px 20px;
    background-color: #000;
    color: #fff;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.submit-btn:hover {
    background-color: #333;
}
