.footer {
    background-color: #f7f7f7;
    color: white;
    padding: 2rem 0;
    box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
    bottom: 0;
    width: 100%;
}

.footer-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
}

.footer-text h2 {
    font-size: 2rem;
    font-weight: 700;
    margin: 0;
    color: #000000;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.footer-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    padding-top: 1rem;
    width: 100%;
}

.copyright {
    font-size: 1rem;
    color: #000000cc;
    margin: 0;
}

/* Адаптивные стили для мобильных устройств */
@media (max-width: 768px) {
    .footer-content {
        gap: 1rem;
    }

    .footer-text h2 {
        font-size: 1.5rem;
    }

    .copyright {
        font-size: 0.875rem;
    }
}
