html {
  scroll-behavior: smooth;
}
* {
  box-sizing: border-box;
}

/*=== Container ===*/
body {
  font-family: 'Inter', sans-serif;
  background-color: #b2b2b2;
  margin: 0;
  padding: 0;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;

  text-align: center;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}


/*=== Btn ===*/
.btn {
  background-color: #111;
  color: #fff;
  padding: 15px 30px;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1em;
  transition: background-color 0.3s ease;
}
.btn:hover {
  background-color: #333;
}


/*=== Text ===*/
h1 {
  font-size: 2.5em;
  color: #111;
}
h1 span {
  font-weight: bold;
  color: #000;
}

p {
  color: #7a7a7a;
  font-size: 1.2em;
  margin: 20px 0;
}



label {
  font-size: 1rem;
  color: #333;
  margin-bottom: 5px;
}
input {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/*=== Form`s ===*/
.form-container {
  margin-bottom: 20px;
}
.form-group {
  margin-bottom: 15px;
}
.form-group label {
  display: block;
  margin-bottom: 5px;
}
.form-group input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}