/*===== Адаптация Hero.css =====*/
@media (max-width: 768px) {
    .hero h1 {
        font-size: 25px;
    }
    .hero p {
        font-size: 15px;
    }
}

/*===== Адаптация Build.css =====*/
@media (max-width: 768px) {
    .title {
        font-size: 25px;
    }    
    .subtitle {
        font-size: 15px;
    }

    .form-row {
        width: 60%;
    }
}

/*===== Адаптация Features.css =====*/
@media (max-width: 768px) {
    .features-row {
        flex-direction: column;
        align-items: center;
    }
    .feature {
        max-width: 100%;
        margin-bottom: 40px;
    }
}

@media (max-width: 480px) {
    .feature h2 {
        font-size: 16px;
    }
    .feature p {
        font-size: 12px;
    }
}

/*===== Адаптация Generator.css ======*/
@media (max-width: 810px) {
  .result-card {
    padding: 1rem;
  }

  .result-title {
    font-size: 1.25rem;
  }

  .result-grid {
    grid-template-columns: 1fr;
  }

  .result-item {
    padding: 0.75rem;
  }

  .result-label {
    font-size: 0.75rem;
  }

  .result-value {
    font-size: 1rem;
  }

  .generator-content {
    flex-direction: column;
  }
}

@media (min-width: 641px) and (max-width: 1024px) {
  .result-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1025px) {
  .result-card {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }
}


/*===== Адаптация Footer.css =====*/
@media (max-width: 768px) {
    .footer-text,
    .footer-bottom {
      flex-direction: column;
      align-items: center;
    }
  }